body {
	padding: 0;
	margin: 0;
}

* {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
::-moz-selection {
	background: #007aff30;
}
::selection {
	background: #007aff30;
}
::-moz-selection {
	background: #007aff30;
}

/* iPad */
@media screen and (min-width: 767px) and (max-width: 1260px) {
	.search-input {
		width: 97% !important;
	}
	.editor-navigation {
		grid-template-columns: 0fr 1fr 1fr 1fr !important;
	}
	.file-pane {
		position: fixed;
		left: -2000px;
		bottom: 0;
		top: 50px;
		box-shadow: 2px 6px 15px rgba(0, 0, 0, 0.25);
	}
	.result-pane {
		position: fixed;
		z-index: 200;
		height: 500px !important;
		right: 80px;
		top: 45px;
		border-bottom-left-radius: 10px;
		border-bottom-right-radius: 10px;
		box-shadow: -1px 5px 16px rgba(0, 0, 0, 0.27) !important;
		border: 2px solid #ccc;
	}
}
/* iPad Portrait */
@media only screen and (min-device-width: 767px) and (max-device-width: 1260px) and (orientation: portrait) {
	.code-pane {
		border: none !important;
	}
}
/* iPad Landscape */
@media only screen and (min-device-width: 767px) and (max-device-width: 1260px) and (orientation: landscape) {
}
/* Phone Responsiveness */
@media screen and (min-width: 120px) and (max-width: 767px) {
	.editor-title {
		display: none !important;
	}
	.file-pane input {
    width: 92% !important;
	}
	.file-list li.open-folder > ul > li {
		width: 96% !important;
	}
	#download-result h1 {
		opacity: 0 !important;
	}
	.modal .large-btn {
		margin: 10px 20px !important;
	}
	.search-container input {
		width: 70% !important;
		min-width: auto !important;
		margin-left: 8px !important;
	}
	.editor-navigation > div:last-child {
		width: 200px;
	}
	.editor-navigation {
		grid-template-columns: 0fr 0fr 1fr 1fr !important;
	}
	.file-pane {
		position: fixed;
		left: -2000px;
		bottom: 0;
		top: 50px;
		right: 0;
		width: 100% !important;
		overflow-x: hidden !important;
	}
	.code-pane,
	.result-pane {
		border: none !important;
	}
	.code-pane {
		border: none !important;
	}
	.error-messages {
		bottom: 0;
		left: 0;
		padding: 0 !important;
		position: relative !important;
		margin: 0 !important;
	}
	.e-message {
		border-radius: 0 !important;
		display: block;
		border: none !important;
		padding: 15px 0 !important;
		padding-left: 40px !important;
		margin: 0 !important;
	}
	.search-action-results {
		position: fixed !important;
		top: 51px !important;
		left: 0 !important;
		right: 0 !important;
		bottom: 0;
		box-shadow: none !important;
		border-radius: 0 !important;
		width: auto !important;
		max-height: 100% !important;
	}
	.modal {
		width: 100% !important;
		padding: 0 !important;
		border-radius: 0 !important;
		position: absolute;
		bottom: 0;
		top: 0;
		left: 0;
		right: 0;
		overflow: auto;
	}
	.modals-container {
		justify-content: unset !important;
		align-items: unset !important;
	}
	#create-edit-shortcut {
		padding: 20px !important;
		width: auto !important;
	}
	.ios-tabs {
		grid-template-columns: repeat(auto-fill,minmax(150px,1fr)) !important;
	}
	#create-edit-shortcut .large-btn {
		margin: 10px 0 !important;
	}
}
/* General Responsiveness */
@media screen and (min-width: 120px) and (max-width: 1260px) {
	.editor-menu {
		display: none !important;
	}
	.glyphs-container {
		height: 50vh !important;
	}
	.result-pane {
		position: fixed;
		z-index: 200;
		top: 50px;
		width: auto !important;
		right: 0 !important;
		left: 0;
		bottom: 0;
		padding-bottom: 0 !important;
		border-radius: 0;
		border: none !important;
		height: 95vh !important;
	}
	.result-text {
		border-left: none !important;
	}
	.download-or {
		display: none !important;
	}
	.editor-container {
		display: block !important;
		grid-template-columns: auto !important;
		position: static !important;
		margin-top: 50px;
	}
	.code-pane {
		height: 93vh;
	}
	.download-grid > div:first-child:after {
		display: none;
	}
	.result-pane.mobiledisabled {
		display: none;
	}
	.close-search-btn {
		display: none !important;
	}
	.close-mobile-search-btn {
		display: block !important;
	}
	#run-preview {
		display: inline-block !important;
	}
	.loading-result-progress {
		right: 0 !important;
		left: 0;
		width: auto !important;
	}
	.file-pane.open-menu {
		left: 0;
	}
	.result-details {
		display: none !important;
	}
	.editor-navigation div:nth-child(2) > .editor-btn {
		display: none !important;
	}
	.mobile-filemenu {
		display: block !important;
	}
	.download-grid {
		display: block !important;
	}
	.download-grid > div:first-child {
		border-right: none !important; /* border-bottom: 1px solid #eee; */
	}
	.download-grid > div:last-child {
		display: none;
	}
	.code-pane #ace_editor {
		height: 100vh !important;
	}
}
/**/
html,
body {
	font-family: -apple-system, BlinkMacSystemFont, Helvetica, Arial, sans-serif;
}
hr {
	border: 0;
	height: 0;
	border-top: 1px solid rgba(0, 0, 0, 0.1);
	border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
a:link {
	color: #007aff;
	text-decoration: none;
}
a:visited {
	color: #007aff;
}
a:hover,
a:active,
a:focus {
	text-decoration: underline;
	color: #007aff;
	cursor: pointer !important;
}
button {
	cursor: pointer;
	outline: none;
}
.upload-area {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	position: fixed;
	z-index: 500;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: #b8daffd1;
	font-size: 25px;
	font-weight: bold;
	color: #38618e70;
	border: 5px dashed #007aff50;
}
.upload-area div {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}
#download-result {
	text-align: center;
}
.details-text {
	font-size: 14px;
	color: #888;
	margin: 10px 0;
}
.modals-container {
	display: none;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: 1000;
}
.modal {
	margin: 0 auto;
	display: none;
	width: 600px;
	background-color: #fff;
	border-radius: 15px;
	padding: 10px 23px;
	box-shadow: 2px 2px 50px rgba(0, 0, 0, 0.2);
}
.modal .large-btn {
	margin: 10px 0;
}
.modal.dialog {
	width: 400px;
}
.modal h1 {
	display: inline-block;
}
.editor-window {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}
/* inputs */
input,
textarea,
[contenteditable] {
	caret-color: #007aff;
}
.input-label {
	font-weight: 500;
	color: #555;
	font-size: 15px;
	line-height: 22px;
	margin: 15px 0;
}
.color-label {
	margin-bottom: 0;
}
input {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	padding: 10px;
	background-color: #ebebec;
	width: -webkit-fill-available;
	width: -moz-available;
	width: stretch;
	border-radius: 8px;
	border: none;
	font-size: 14px;
	margin: 10px 0;
	min-width: 200px;
	outline: none;
	-webkit-transition: 0.3s background-color;
	transition: 0.3s background-color;
}
input:active {
	background-color: #8c99b350 !important;
}
.search-input {
	background: #ebebec url(/static/media/search.d90afa47.svg) 8px center / 20px no-repeat !important;
	padding-left: 35px !important;
	margin-bottom: 5px !important;
	margin-top: 1px !important;
	width: 93%;
}
/* required field errors */
.require-error {
	background: url(/static/media/error.e021e5c8.svg) left center / 16px no-repeat;
	padding-left: 23px;
	color: #d74c39;
	margin-top: 10px;
	font-size: 14px;
}
/* ios tabs */
.ios-tabs {
	display: grid;
	grid-template-columns: repeat(auto-fill,minmax(200px,1fr));
	margin: 5px 0;
}
.ios-tabs button {
	background: none;
	text-align: center;
	display: inline-block;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	font-size: 14px;
	color: #007aff;
	border: 0.5px solid #007aff;
	border-right: none;
	padding: 8px 0;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
}
.ios-tabs button.active-itab {
	background-color: #007aff;
	color: #fff;
}
.ios-tabs button:first-child {
	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
}
.ios-tabs button:last-child {
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	border-right: 0.5px solid #007aff;
}
/* navigation + nav elements */
.mobile-filemenu {
	background: url(/static/media/menu.fad57a9b.svg) center / 27px no-repeat;
	padding: 20px;
	height: 8px;
}
.open-filemenu {
	background-image: url(/static/media/back.0659de05.svg);
	background-size: 20px;
}
.editor-navigation {
	background-color: #fff;
	border-bottom: 0.5px solid #c7c7cc;
	display: grid;
	grid-template-columns: 1fr 1fr 0.8fr;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 500;
	height: 50px;
}
.editor-navigation > div:last-child {
	text-align: right;
}
.editor-title {
	padding: 15px;
	font-weight: bold;
	display: inline-block;
	background: url(/static/media/icon.dc2ce2e9.png) left center / 30px no-repeat;
	padding-left: 37px;
	margin-left: 10px;
}
.result-details {
	color: #999;
	margin: 10px 0;
	border-right: 1px solid #eee;
	padding-right: 15px;
	display: inline-block;
}
.result-actions {
	padding: 5px 10px;
	background-color: #eee;
	color: #555;
	font-weight: 600;
	margin-right: 5px;
	border-radius: 5px;
	display: inline-block;
	font-size: 15px;
}
/* download section */
.download-or {
	color: #999;
	display: inline-block;
	padding: 10px !important;
	font-weight: bold;
	text-transform: uppercase;
	margin-left: -22px;
	margin-top: 90px;
	background: #fff;
	height: 20px;
}
/* link buttons */
.mobile-download {
	display: none;
}
.editor-btn {
	display: inline-block;
	cursor: pointer;
	font-size: 15px;
}
.editor-btn a {
	color: #007aff;
	text-decoration: none;
	padding: 15px;
	display: inline-block;
}
.editor-btn.primary-btn {
	font-weight: bold;
}
.editor-btn:hover {
	opacity: 0.8;
}
.editor-btn:active {
	opacity: 0.5;
}
.qr-btn {
	background: url(/static/media/qr.bcdf5d8c.svg) 12px center / 25px no-repeat;
	padding-left: 45px !important;
}
/* editor + result panes */
.editor-container {
	display: grid;
	grid-template-columns: 0fr 1fr 0fr;
	position: fixed;
	top: 50px;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 100;
}
.code-pane {
	height: 95vh;
	overflow: hidden !important;
}
.file-pane,
.code-pane,
.result-pane {
	overflow: auto;
	-webkit-overflow-scrolling: touch;
}
.result-pane {
	border-left: 0.5px solid #c7c7cc;
	background-color: #e1e3e6;
	box-shadow: inset 0 0 10px rgba(150, 150, 150, 0.5);
	padding-top: 0;
	-webkit-transition: 0.3s opacity;
	transition: 0.3s opacity;
	width: 330px;
	height: -webkit-fill-available;
	height: -moz-available;
	height: stretch;
	overflow-y: auto;
}
.result-pane.loading {
	pointer-events: none;
}
.result-text {
	font-size: 14px;
	color: #777;
	background: #eee;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	padding: 10px;
	z-index: 5;
	border-left: 0.5px solid #c7c7cc;
	border-bottom: 0.5px solid #c7c7cc;
}
.styles_container__2XUEs {
	padding: 20px;
	padding-top: 0;
}
#run-preview {
	display: none;
}
/* ace editor */
#ace_editor {
	position: relative;
	top: 0;
	left: 0;
	bottom: 0;
	width: 100% !important;
	height: 95% !important;
	cursor: text;
}
.ace_gutter-cell.ace_error {
	background: url(/static/media/error_x.f4f9364d.svg) 2px 1px / 14px no-repeat !important;
}
.ace_invalid {
	background-color: #d74c39 !important;
}
.ace_active-line {
	background-color: #e8f6fe !important;
}
.ace_cursor {
	color: #007aff !important;
}
/* download modal */
.generate-code-load .load {
	margin: 30px auto;
}
#qr-result {
	width: 150px;
	height: 150px;
	border: 2px solid #c7c7cc;
	border-radius: 3px;
	margin: 0 auto;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: white;
}
#qr-result .qrresimg {
	width: 150px;
	height: 150px;
	background-size: cover;
	image-rendering: -moz-crisp-edges;
	image-rendering: pixelated;
}
#download-shortcut-link {
	text-decoration: none;
	text-align: center;
}
#no-file-msg {
	padding: 80px 0;
	border: 1px solid #eee;
	border-radius: 10px;
	background: #fafafa;
	margin-bottom: 20px;
}
.shortcut-filename {
	margin: 5px;
	font-size: 18px;
	font-weight: 500;
	color: #000;
}
.shortcut-filedetails {
	margin: 5px;
	font-size: 15px;
	color: #888;
	text-transform: uppercase;
}
.download-grid {
	display: grid;
	grid-template-columns: 1fr 0fr 1fr;
	text-align: center;
}
.download-grid > div {
	padding: 20px;
}
.download-grid > div:first-child {
	border-right: 1px solid #eee;
	display: flex;
	justify-content: center;
	align-items: center;
}
.btn {
	padding: 10px 20px;
	background-color: #eee;
	border-radius: 5px;
	margin: 5px;
	font-size: 16px;
	color: #007aff;
	font-weight: 500;
	cursor: pointer;
	border: none;
	text-align: center;
	-webkit-transition: 0.3s all;
	transition: 0.3s all;
}
.btn:hover {
	background-color: #f5f5f5;
}
.btn:active {
	opacity: 0.7;
}
.large-btn {
	font-size: 18px;
	padding: 15px 20px;
	font-weight: bold;
}
.trans-btn {
	background: none;
}
.cancel-btn {
	font-weight: normal;
	background: none;
	float: right;
	margin-top: 23px;
	color: #007aff;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.cancel-btn:hover {
	opacity: 0.7;
}
.cancel-btn:active {
	opacity: 0.5;
}
.too-many-actions {
	position: absolute;
	z-index: 100;
	right: 0;
	top: 0;
	bottom: 0;
	width: 330px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #f8f8f8a3;
}
.too-many-actions div {
	padding: 10px;
	text-align: center;
	font-weight: 500;
	font-size: 15px;
}
.too-many-actions p {
	margin-bottom: 25px;
}
/* loading indicator */
.result-pane.loading .loading-result-progress {
	display: flex;
}
.loading-result-progress {
	display: none;
	position: absolute;
	z-index: 500;
	right: 0;
	top: 45%;
	width: 355px;
}
.result-pane.loading > div {
	opacity: 0.5;
}
.loading-result-progress > div {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
}
.load {
	border-bottom: 3px solid rgba(150, 150, 150, 0.2);
	border-left: 3px solid rgba(150, 150, 150, 0.2);
	border-right: 3px solid rgba(0, 122, 255, 1);
	border-top: 3px solid rgba(0, 122, 255, 1);
	border-radius: 100%;
	height: 30px;
	width: 30px;
	-webkit-animation: rot 0.5s infinite linear;
	animation: rot 0.5s infinite linear;
}
.large {
	height: 50px;
	width: 50px;
}
.small {
	height: 20px;
	width: 20px;
	border-width: 3px !important;
}
@-webkit-keyframes rot {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(359deg);
		        transform: rotate(359deg);
	}
}
@keyframes rot {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(359deg);
		        transform: rotate(359deg);
	}
}

/*Error color*/
.error {
	/*Don't remove*/
	background: #ffdcdc !important;
	/*Don't remove*/
}
.error-messages {
	position: absolute;
	z-index: 110;
	right: 0;
	margin: 15px;
}
.e-message {
	background: url(/static/media/error_w.b2684123.svg) 10px center / 20px no-repeat;
	background-color: #d74c39;
	border: 2px solid #d74c3950;
	padding: 13px 20px;
	padding-left: 40px;
	border-radius: 5px;
	color: #fff;
	font-size: 13px;
	margin-bottom: 13px;
	box-shadow: 0 0 5px rgba(150, 150, 150, 0.5);
	display: grid;
	grid-template-columns: 1fr 0fr;
	max-width: 400px;
	font-weight: 500;
}
.e-message .jump-error {
	padding: 0 10px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.e-message button {
	background-color: #fff;
	border: none;
	font-size: 14px;
	color: #d74c39;
	border-radius: 5px;
	font-weight: bold;
	padding: 8px 20px;
	-webkit-transition: 0.3s opacity;
	transition: 0.3s opacity;
}
.e-message button:active {
	opacity: 0.7;
}
/* Tabs */
.file-tabs {
	border-bottom: 0.5px solid #adacac;
	background: #ccc;
}
.tab {
	display: inline-block;
	padding: 5px 10px;
	padding-right: 20px;
	border-right: 0.5px solid #adacac;
	color: #3d3c3d;
	-webkit-transition: 0.1s all;
	transition: 0.1s all;
	cursor: pointer;
	width: 20%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	margin-bottom: -3px;
}
.tab:hover:not(.active-tab) {
	background: #a7a8a750;
}
.tab.active-tab {
	background: #e3e4e3;
	color: #1d1d1c;
}
.tab-close,
.tab-label {
	display: inline-block;
}
.tab-close {
	font-size: 20px;
	line-height: 12px;
	border-radius: 3px;
	color: #454445;
	height: 15px;
	width: 15px;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	padding: 3px;
	text-align: center;
	-webkit-transition: 0.1s all;
	transition: 0.1s all;
}
.tab-close:active {
	opacity: 0.7;
}
.tab.active-tab .tab-close:hover {
	background: #ccc;
}
.tab .tab-close:hover {
	background: #aaa;
}
.tab-label {
	font-size: 13px;
	line-height: 15px;
	margin-left: 10px;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
/* Menus */
.editor-menu {
	display: inline-block;
	color: #007aff;
	cursor: pointer;
	-webkit-transition: 0.2s all;
	transition: 0.2s all;
}
.editor-menu ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	padding: 16px;
	display: inline-block;
	font-size: 15px;
}
.editor-menu > ul:first-child:hover {
	background: #e8f6fe90;
}
.editor-menu ul ul {
	margin-left: -15px;
	position: absolute;
	padding: 10px 0;
	padding-left: 1px;
	background-color: #fff;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	margin-top: 16px;
	box-shadow: 0 3px 10px rgba(100, 100, 100, 0.3);
