/* File pane */
.file-pane {
	width: 300px;
	background-color: #fff;
	border-right: 0.5px solid #c7c7cc;
	-webkit-transition: 0.1s left ease-out;
	transition: 0.1s left ease-out;
	z-index: 100;
	padding-bottom: 20px;
	overflow-x: hidden;
	overflow-y: auto;
	height: -webkit-fill-available;
	height: -moz-available;
	height: stretch;
}
.file-pane input {
	width: 90%;
	margin-left: 15px;
}
.file-pane h2 {
	margin: 20px 15px;
}
/* Files Navigation */
.files-header {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	background-color: #fafafa;
	height: 85px;
	border-bottom: 1px solid #ddd;
	margin-bottom: 10px;
	padding: 15px 0;
	box-shadow: 0px -5px 20px rgba(0, 0, 0, 0.15);
}
.file-btns {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	padding: 0 10px;
}
.file-btn {
	font-size: 15px;
	padding: 20px 0;
	width: 100%;
	width: -webkit-fill-available;
	width: -moz-available;
	width: stretch;
	padding-left: 30px;
	font-weight: normal;
	outline: none;
}
.upload-btn {
	background: #eee url(data:image/png;base64,) center / 25px no-repeat;
}
.new-btn {
	background: #eee url(/static/media/new_file.d7ae7f30.png) center / 25px no-repeat;
}
.newf-btn {
	background: #eee url(/static/media/new_folder.7aa38579.png) center / 25px no-repeat;
}
/* File list */
.file-list li {
	padding: 8px 10px;
	padding-left: 25px;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	-webkit-transition: 0.3s background-color;
	transition: 0.3s background-color;
}
.file-list li:hover:not(.open-folder) {
	background-color: #e8f6fe !important;
}
.file-list li:active:not(.open-folder) {
	background-color: #daeffc !important;
}
.file-list li.open-folder > ul > li {
	width: 94%;
}
.file-list ul {
	list-style-type: none;
	padding: 0;
	margin: 0;
}
.file-list ul ul {
	border-left: 1px solid #ddd;
	margin: 5px 10px;
}
.list-item-file > div,
.list-item-folder > div {
	padding-left: 27px;
	font-size: 14px;
}
.item-name {
	width: 70%;
	height: 18px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	display: inline-block;
	margin-top: 3px;
}
.item-name .load {
	display: none;
}
.file-list ul li.open-folder ul li > div > .item-name {
	width: 60%;
}
.file-list ul li.open-folder ul li.open-folder ul > li > div > .item-name {
	width: 50%;
}
/* File items */
.list-item-file > div {
	background: url(/static/media/file.2ee41821.png) 2px center / 18px no-repeat;
}
.file-list li.list-item-file.active {
	background-color: #007aff20;
	font-weight: 500;
}
.file-list li.list-item-file.active:hover {
	background-color: #007aff10 !important;
}
.file-list li.list-item-file.active > div {
	background-image: url(/static/media/file_active.483c7c4d.png);
}
/* uploading state */
.list-item-file.uploading {
	background: none;
	pointer-events: none;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	color: #777;
}
.list-item-file.uploading .action-btns {
	opacity: 0.5;
}
.list-item-file.uploading > div {
	background: none;
}
.list-item-file.uploading .item-name .load {
	display: inline-block;
	position: absolute;
	margin-left: -25px;
	margin-top: 2px;
	width: 10px;
	height: 10px;
}
/* Folder items */
.list-item-folder > div {
	background: url(/static/media/folder.aab3fbba.png) left center / 20px no-repeat;
}
.file-list li.list-item-folder > ul {
	display: none;
	-webkit-transform-origin: top center;
	        transform-origin: top center;
	-webkit-transition: 0.1s left ease-out;
	transition: 0.1s left ease-out;
	-webkit-transform: scale(1, 0);
	        transform: scale(1, 0);
}
.file-list li.list-item-folder {
	background: url(/static/media/right.a86a80c5.svg) 10px 15px / 10px no-repeat;
}
.file-list li.list-item-folder.open-folder {
	background: url(/static/media/down.e4118b45.svg) 10px 15px / 10px no-repeat;
	padding-bottom: 0;
}
.file-list li.list-item-folder.open-folder > ul {
	display: block;
	-webkit-animation: scaleIn 0.1s;
	        animation: scaleIn 0.1s;
	-webkit-transform: scale(1, 1);
	        transform: scale(1, 1);
}
.list-item-folder.open-folder > div {
	font-weight: 500;
}
@-webkit-keyframes scaleIn {
	from {
		-webkit-transform: scale(1, 0);
		        transform: scale(1, 0);
	}
	to {
		-webkit-transform: scale(1, 1);
		        transform: scale(1, 1);
	}
}
@keyframes scaleIn {
	from {
		-webkit-transform: scale(1, 0);
		        transform: scale(1, 0);
	}
	to {
		-webkit-transform: scale(1, 1);
		        transform: scale(1, 1);
	}
}
/* Rename and delete btns */
.action-btns {
	float: right;
	margin-top: -3px;
}
.file-list ul ul ul .action-btns {
	margin-right: 3px;
}
.edit-btn,
.delete-btn {
	background: url() center / 20px no-repeat;
	padding: 15px;
	float: right;
	border-radius: 3px;
	cursor: pointer;
}
.action-btns div:active {
	background-color: #8c99b3;
}
.edit-btn {
	background-image: url(/static/media/edit.e0815966.svg);
}
.edit-btn:active {
	background-image: url(/static/media/edit_w.f67db0e8.svg);
}
.delete-btn {
	background-image: url(/static/media/delete.b4f31ac8.svg);
	margin-left: 3px;
}
.delete-btn:active {
	background-image: url(/static/media/delete_w.2b6a14fb.svg);
}

/* Search actions */
.search-container {
	padding-top: 5px;
}
.search-container input {
	width: 75%;
}
.close-search-btn {
	padding: 5px;
	font-size: 15px;
	text-align: center;
	display: inline-block;
	margin-left: 10px;
	color: #007aff;
	cursor: pointer;
}
.close-mobile-search-btn {
	float: right;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	padding: 10px 20px;
	font-size: 30px;
	text-align: center;
	display: inline-block;
	background-color: #fafafa;
	color: #007aff;
	cursor: pointer;
	display: none;
	border-bottom-left-radius: 10px;
	border-left: 0.5px solid #c7c7cc50;
	border-bottom: 0.5px solid #c7c7cc50;
}
.close-search-btn a {
	text-decoration: none !important;
}
.search-action-results {
	position: absolute;
	opacity: 0;
	pointer-events: none;
	cursor: pointer;
	z-index: 2000;
	margin: 0 auto;
	top: 50px;
	right: 0;
	left: 0;
	width: 500px;
	max-height: 500px;
	overflow-y: scroll;
	background-color: #fff;
	border-radius: 8px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
	-webkit-transition: 0.3s opacity;
	transition: 0.3s opacity;
}
.search-action-results.search-action-results-visible {
	opacity: 1;
	pointer-events: all;
}
.action-item {
	padding: 15px 20px;
	padding-top: 18px;
	padding-left: 40px;
	background: url(data:image/png;base64,) 6px 14px / 30px no-repeat;
	-webkit-transition: 0.3s background-color;
	transition: 0.3s background-color;
}
.action-item:hover {
	background-color: #fafafa;
}
.action-item:active {
	background-color: #007aff20;
}
.search-action-results .action-item:not(:last-child) {
	border-bottom: 0.5px solid #eee;
}
.action-item-title {
	font-weight: bold;
	display: inline-block;
}
.action-item-code {
	display: inline-block;
	margin: 0 10px;
	background-color: rgba(150, 150, 150, 0.2);
	border: 1px solid #ddd;
	padding: 3px 8px;
	border-radius: 3px;
	font-size: 13px;
	font-family: monospace;
	font-weight: normal;
}
.action-item-description {
	font-size: 14px;
	color: #555;
	margin: 8px 0;
}
.action-item-url {
	font-size: 14px;
}
.action-item-usage {
